@import "../../../constants.module.scss";

$transition: .2s;
.main-menu {
  grid-column-start: 1;
  position: fixed;
  align-items: center;
  justify-content: center;
  width: 76px;
  height: 64px;
  background: var(--bgThemeNavBarColor);
  z-index: 91;
}

.menu__content {
  grid-column-start: 1;
  grid-row-start: 2;
  position: fixed;
  left: 0;
  top: 0;
  width: 76px;
  height: 100%;
  background: var(--bgThemeNavBarColor);
  box-shadow: var(--bgNavBarShadow);
  transition: .1s;
  z-index: 90;
}

.wrapper-menu {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  top: 54px;
  padding-top: 10px;
  padding-left: 15px;

  &:hover {
    display: grid;
    grid-gap: 120px;
    background: var(--bgThemeNavBarColor);
    overflow: auto;
    overflow-x: hidden;
  }

  &::-webkit-scrollbar {
    position: absolute;
    width: 8px;
    background-color: var(--themeNavBarBgHover);
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 8px;
    background-color: var(--KebabCardThemeColor);
  }
}

.wrapper-menu-active {
  display: grid;
  grid-gap: 120px;
  background: var(--bgThemeNavBarColor);
  overflow: auto;
  overflow-x: hidden;
}

.menu__content:hover, .menu__content--active {
  width: 264px;
  transition: .1s;
}

.menu__inner {
  position: relative;
  top: 0;
  width: 100%;
  height: 100%;
  padding: 0;
  margin-top: 10px;
}

.menu__item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  margin: 0 0 22px 0;
  list-style-type: none;
}

.menu__item-footer {
  margin: 12px 0;
}

.menu__item-inner {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 232px;
  border-radius: 8px;
  text-decoration: none;
}

.menu__item-inner:hover {
  background: var(--themeNavBarBgHover);
  transition: $transition;
}

.menu__item-inner:hover .menu__text {
  color: var(--themeNavBarColorHover);
  transition: $transition;
}

.menu__item-inner:hover .svg__item {
  fill: var(--themeNavBarColorHover);
  transition: $transition;
}

.menu__item-inner--active.menu__item-inner--selected {
  background: var(--themeNavBarBgHover);
}

.menu__content:hover .menu__item-inner--selected {
  background: var(--themeNavBarBgHover);
  transition: $transition;
}

.menu__footer {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  left: 0;
  bottom: 64px;
  width: 100%;
  padding-top: 10px;
}

.menu__footer-contacts {
  display: none;
  flex-direction: column;
  align-items: flex-start;
  width: 0;
  transition: $transition;
}

.menu__footer-contacts--active, .menu__content:hover .menu__footer-contacts {
  display: flex;
  width: 232px;
  border-bottom: 1px solid var(--iconsTextThemeNavBarColor);
}

.footer__contacts-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-left: 12px;
}

.footer__contacts-title {
  margin: 0 0 16px 0;
}

.footer__contacts-number, .footer__contacts-social {
  margin: 0 0 12px 0;
}

.footer__contacts-social {
  display: flex;
}

.footer__social-item {
  display: flex;
}

.menu__text, .footer__contacts-number, .footer__contacts-title {
  width: 0;
  color: var(--iconsTextThemeNavBarColor);
  font-family: $font;
  font-size: 0;
  font-weight: 400;
  text-decoration: none;
  text-align: left;
  opacity: 0;
  transition: $transition;
}

.menu__text--active, .footer__contacts-number--active, .footer__contacts-title--active, .menu__content:hover .menu__text,
.menu__content:hover .footer__contacts-number, .menu__content:hover .footer__contacts-title {
  width: 140px;
  font-size: 1em;
  opacity: 1;
  transition: $transition;
}

.footer__contacts-number:hover {
  color: var(--themeNavBarColorHover);
}

.menu__text--selected {
  color: var(--textThemeSideBarColorHover);
}

.menu__footer-lginout {
  width: 44px;
  border-bottom: 1px solid var(--iconsTextThemeNavBarColor);
  transition: .1s;
}

.menu__footer-lginout--active, .menu__content:hover .menu__footer-lginout {
  width: 232px;
  border-bottom: 1px solid var(--iconsTextThemeNavBarColor);
  transition: $transition;
}

.menu__footer-text {
  display: none;
  color: var(--iconsTextThemeNavBarColor);
  text-align: left;
  font-weight: 400;
  font-size: 0;
  line-height: 150%;
  text-decoration: none;
  transition: .1s;
}

.menu__footer-rights {
  margin-bottom: 16px;
}

.menu__footer-text--active, .menu__content:hover .menu__footer-text, .menu__footer-date {
  width: 100%;
  display: revert;
  color: var(--iconsTextThemeNavBarColor);
  text-align: left;
  font-weight: 400;
  font-size: 12px;
  line-height: 150%;
  transition: $transition;
}

.menu__content:hover .menu__footer-policy:hover {
  color: var(--themeNavBarColorHover);
  text-decoration: underline;
}

.image__wrapper, .image__wrapper-theme, .image__wrapper-footer, .menu__content:hover .image__wrapper-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  margin-right: 10px;
  border-radius: 50%;
  opacity: 1;
  cursor: pointer;
  transition: 0.2s;
}

span {
  margin-bottom: 0;
}

.image__wrapper-theme {
  width: 42px;
  height: 42px;
  border: 1px solid var(--iconsTextThemeNavBarColor);
  transition: 0.1s;
}

.image__wrapper-footer:hover .svg__item {
  fill: var(--themeNavBarColorHover);
  transition: $transition;
}

.image__wrapper-footer--active {
  width: 0;
  opacity: 0;
}

.image__wrapper--active:hover, .image__wrapper-theme--active:hover {
  background: var(--themeNavBarBgHover);
  transition: $transition;
}

.image__wrapper-theme--active:hover {
  border: 1px solid var(--themeNavBarColorHover);
  transition: $transition;
}

.image__wrapper--active.image__wrapper--selected, .image__wrapper-theme--active.image__wrapper-theme--selected {
  background: var(--themeNavBarBgHover);
}

.menu__item-inner:hover .image__wrapper-theme {
  border: 1px solid var(--themeNavBarColorHover);
}

.menu__content:hover .image__wrapper, .menu__content:hover .image__wrapper-theme {
  background: none;
}

.menu__item-inner-theme:hover {
  background: none;
}

.image__wrapper-theme.image__wrapper-theme--selected {
  border: 1px solid var(--themeNavBarColorHover);
}

.image__wrapper--active:hover .svg__item, .image__wrapper-theme--active:hover .svg__item {
  fill: var(--themeNavBarColorHover);
  transition: $transition;
}

.image__wrapper--active:hover .svg__menu, .image__wrapper-theme--active:hover .svg__menu {
  stroke: var(--themeNavBarColorHover);
  transition: $transition;
}

.svg__item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  fill: var(--iconsTextThemeNavBarColor);
}

.svg__item svg {
  width: 24px;
  height: 24px;
}

.svg__menu {
  stroke: var(--iconsTextThemeNavBarColor);
}

.svg__menu--active, .menu__content:hover .svg__menu {
  stroke: var(--themeNavBarColorHover);
  transition: $transition;
}

.svg__item--active.svg__item--selected {
  fill: var(--themeNavBarColorHover);
}

.svg__item--selected {
  fill: var(--themeNavBarColorHover);
}